import React from "react"

export default props => (

    <li className="project">
        <a href={props.projectUrl}>
            <img src={props.imgSrc} alt={props.imgAlt} width={props.imgWidth}/>

            <h3 style={{color:"white"}}>{props.name}</h3> 
                
            
            <p style={{color:"white"}}>{props.description}</p>
            <p style={{padding:"1vw",marginTop:"1vw",margin:"2vw",float:"left",borderRadius:"5px",backgroundColor:"white",color:"black"}}>
                  {props.type}</p>
        </a>
    </li>


)